<template>
  <tr class="user-ride-history-row">
    <td class="user-ride-history-row__col">
      <UiTextString size="s" weight="700">{{ id }}</UiTextString>
    </td>
    <td class="user-ride-history-row__col">
      <UiTextString size="s" weight="700">
        {{ carName }}
      </UiTextString>
    </td>
    <td class="user-ride-history-row__col">
      <UiTextString size="s" weight="700">
        {{ rideDuration }}
      </UiTextString>
    </td>
    <td class="user-ride-history-row__col">
      <UiTextString size="s" weight="700">
        {{ ridePrice }}
      </UiTextString>
    </td>
    <td class="user-ride-history-row__col">
      <UiTextString size="s" weight="700">{{ datetime }}</UiTextString>
    </td>
    <td class="user-ride-history-row__col">
      <UiTextString size="s" weight="700">
        {{ localStatus }}
      </UiTextString>
    </td>
    <td class="user-ride-history-row__col">
      <ButtonLink>смотреть →</ButtonLink>
    </td>
  </tr>
</template>

<script>
import convertToDatetime from '@/helpers/convert-to-datetime';
import UiTextString from '@/components/core/ui-text-string/ui-text-string.vue';
import ButtonLink from '@/components/button-link/button-link.vue';
import convertMsToTime from '@/helpers/convert-ms-to-time';
import convertCurrency from '@/helpers/convert-currency';

const i18n = {
  finalized: 'Завершен',
};

export default {
  components: {
    UiTextString,
    ButtonLink,
  },

  props: {
    id: {
      type: String,
      required: true,
    },

    carName: {
      type: String,
      required: true,
    },

    duration: {
      type: Number,
      required: true,
    },

    price: {
      type: Number,
      required: true,
    },

    timestamp: {
      type: Number,
      required: true,
    },

    status: {
      type: String,
      required: true,
    },

    link: {
      type: String,
      required: true,
    },
  },

  computed: {
    datetime() {
      return convertToDatetime('%d %b %Y, %H:%M', new Date(this.timestamp));
    },

    rideDuration() {
      return convertMsToTime(this.duration);
    },

    ridePrice() {
      return convertCurrency(this.price);
    },

    localStatus() {
      return i18n[this.status];
    },
  },
};
</script>

<style>
.user-ride-history-row__col {
  padding: 15px 0;
}
</style>
