<template>
  <UiContainer class="small-card">
    <div class="small-card__image">
      <img :src="src" alt="" />
    </div>
    <UiTextString class="small-card__title" size="xl" weight="700">
      {{ title }}
    </UiTextString>
    <component
      class="small-card__link"
      :is="tag"
      color="grey"
      @click="tag === 'ButtonLink' && $emit('click')"
    >
      {{ caption }}
    </component>
  </UiContainer>
</template>

<script>
import UiContainer from '@/components/core/ui-container/ui-container.vue';
import UiTextString from '@/components/core/ui-text-string/ui-text-string.vue';
import ButtonLink from '@/components/button-link/button-link.vue';

export default {
  components: {
    UiContainer,
    UiTextString,
    ButtonLink,
  },

  props: {
    src: {
      type: String,
      required: true,
    },

    title: {
      type: [String, Number],
      required: true,
    },

    caption: {
      type: String,
      required: false,
      default: '',
    },

    link: {
      type: String,
      required: false,
      default: '',
    },
  },

  computed: {
    tag() {
      return this.link ? 'ButtonLink' : 'UiTextString';
    },
  },
};
</script>

<style>
.small-card {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.small-card__title {
  margin-top: 15px;
}

.small-card__link {
  margin-top: 10px;
}
</style>
